import React from 'react'
import { LightSpeed } from 'react-reveal'
import {
	Brightness7,
	Brightness3,
} from '@material-ui/icons'

const Theme = ({ theme, setTheme }) => {
	return (
		<div className='theme'>
			<LightSpeed
				right
				appear
				spy={theme}
				duration={750}
				delay={theme ? 90 : 850}
			>
				<button
					onClick={() => {
						setTheme((t) => !t)
					}}
				>
					{theme ? (
						<Brightness3 />
					) : (
						<Brightness7 />
					)}
				</button>
			</LightSpeed>
		</div>
	)
}

export default Theme
